<template>
  <div class="test_json">
    <v-container>
      <v-layout wrap>
        <v-flex xs12 sm12 md6>
          <v-card width="100%">
            <v-subheader>Selected city from bottm select box</v-subheader>
            <v-btn block>
              <select style="width:100%; text-align:center;" v-on:change="select_city">
                <option large v-for="input_city in input_cities" v-bind:value="input_city" :key="input_city">
                  {{ input_city }}
                </option>
              </select>
            </v-btn>
          </v-card>
          <!-- <v-subheader>Rader chart in {{selected_city_column[0]}}</v-subheader> -->
          <apexchart type=radar height=540px :options="radar_chartOptions" :series="radar_series" />
        </v-flex>
        <v-flex xs12 sm12 md6>
          <div style="height: auto; width: 100%;">
            <v-subheader>
              <p>Selected city: {{selected_city_column[0]}}</p>
              <v-spacer></v-spacer>
              <v-btn center right style="color: blue;" v-on:click="isShow = !isShow"> Show tables
              </v-btn>
              <v-btn center right style="color: red;" v-on:click="reset_input_data">Reset table data</v-btn>
            </v-subheader>

            <v-card style="height: 600px; width: 100%; overflow: auto;">
              <v-list three-line>
                <v-list-tile v-for="(item, index) in first_column_input_data.slice( 1, 18 )" :key="item.id">
                  <div style="width: 100%">
                    <v-subheader>{{first_column_input_data[index+1]}}</v-subheader>
                  </div>
                  <div style="width: 80%">
                    <v-slider v-if="index == 0" min="0" max="10" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 1" min="0" max="100" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 2" min="0" max="100" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 3" min="0" max="500" step="0.1" thumb-label="always" :thumb-size="40"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 4" min="0" max="20" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 5" min="0" max="1" step="0.01" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 6" min="0" max="1" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 7" min="0" max="100" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 8" min="0" max="100" step="0.1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 9" min="0" max="200" step="0.1" thumb-label="always" :thumb-size="40"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 10" min="0" max="1" step="0.01" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 11" min="0" max="100" step="0.01" thumb-label="always"
                      :thumb-size="30" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 12" min="0" max="100000" step="0.1" thumb-label="always"
                      :thumb-size="45" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 13" min="0" max="200000" step="1" thumb-label="always"
                      :thumb-size="45" v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 14" min="0" max="20" step="1" thumb-label="always" :thumb-size="30"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 15" min="0" max="1" step="0.001" thumb-label="always" :thumb-size="40"
                      v-model="selected_city_column[index+1]"></v-slider>
                    <v-slider v-else-if="index == 16" min="0" max="1" step="0.001" thumb-label="always" :thumb-size="40"
                      v-model="selected_city_column[index+1]"></v-slider>
                  </div>
                  <div style="padding : 5%; width: 40%">
                    <v-text-field v-model="selected_city_column[index+1]">
                      {{selected_city_column[index+1]}}</v-text-field>
                  </div>
                </v-list-tile>
              </v-list>
            </v-card>
          </div>
        </v-flex>
      </v-layout>
      <v-layout wrap>
        <v-flex xs12 sm12 md12>
          <v-subheader>Selected city is {{selected_city_column[0]}}</v-subheader>
          <div style="height: 390px; width: 100%; overflow: auto;">
            <l-map :zoom="zoom" :center="selected_city_geodata.slice(1.3)" :attribution="attribution"
              :options="{zoomControl: true}">
              <l-tile-layer :url="url"></l-tile-layer>
              <l-marker :lat-lng="selected_city_geodata.slice(1.3)">
                <l-tooltip>{{selected_city_geodata}}</l-tooltip>
              </l-marker>
            </l-map>
          </div>
        </v-flex>
        <v-flex xs12 sm12 md12 v-show="isShow">
          <div style="height: 500px; width: 100%; overflow: auto;">
            <h2>Input data</h2>
            <hot-table v-model="input_data" :data="input_data" ref="input_data_ref" :autoWrapRow="false"
              :colHeaders="true" :rowHeaders="true" :readOnly="false" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
          </div>

          <div class="box14" style="height: 200px; width: 100%; overflow: auto;">
            <h2>Raws</h2>
            <p>selected_city: {{selected_city}}</p>
            <p>selected_city_column: {{selected_city_column}}</p>
            <p>first_column_input_data: {{first_column_input_data}}</p>
            <p>Max_column:{{Max_column}}</p>
            <p>Min_column:{{Min_column}}</p>
            <p>GHG:{{GHG}}</p>
            <p>mu:{{mu}}</p>
            <p>Social_Welfare:{{Social_Welfare}}</p>
            <p>radar_series:{{radar_series}}</p>
          </div>

          <div style="height: auto; width: 100%; overflow: auto;">
            <h2>Input geodata</h2>
            <hot-table :data="input_geodata" ref="input_geodata_ref" :colHeaders="true" :rowHeaders="true"
              :readOnly="true" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
          </div>
          <h2>Input calculation</h2>
          <hot-table :data="input_calculation" :readOnly="true" ref="input_calculation_ref" :colHeaders="true"
            :rowHeaders="true" style="width: auto; height: auto; overflow: auto"
            licenseKey="non-commercial-and-evaluation"></hot-table>

          <h2>Input graph</h2>
          <hot-table :data="input_graph" :readOnly="true" ref="input_graph_ref" :colHeaders="true" :rowHeaders="true"
            style="width: auto; height: auto; overflow: auto" licenseKey="non-commercial-and-evaluation"></hot-table>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  import input_json_data from '../assets/JSON_Data/input_data.json'
  import input_json_cities from '../assets/JSON_Data/input_cities.json'
  import input_json_calculation from '../assets/JSON_Data/input_calculation_data.json'
  import input_json_graph from '../assets/JSON_Data/input_graph_data.json'
  import input_json_geodata from '../assets/JSON_Data/input_geodata.json'

  import HotTable from 'vue-handsontable-official'
  import compute_gmean from 'compute-gmean'
  import {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
    LIcon,
  } from 'vue2-leaflet';

  function pushTwoDimensionalArray(array1, array2, axis) {
    if (axis != 1) axis = 0;
    if (axis == 0) { // 縦方向の追加
      for (var i = 0; i < array2.length; i++) {
        array1.push(array2[i]);
      }
    } else { // 横方向の追加
      for (var i = 0; i < array1.length; i++) {
        Array.prototype.push.apply(array1[i], array2[i]);
      }
    }
    return array1;
  }

  /*有効数字2桁 method */
  function round2(val) {
    return val.toFixed(2);
  }

  /*大小比較 method */
  function MaxSort(Max_col, col) {
    for (var i = 0; i < Max_col.length; i++) {
      if (col[i] > Max_col[i]) {
        Max_col[i] = col[i];
      }
    }
    return Max_col;
  }

  function MinSort(Min_col, col) {
    for (var i = 0; i < Min_col.length; i++) {
      if (col[i] < Min_col[i]) {
        Min_col[i] = col[i];
      }
    }
    return Min_col;
  }


  /* Get geomean calculation from 1D array method */
  const transpose = a => a[0].map((_, c) => a.map(r => r[c]));

  /* Get geomean calculation from 1D array method */
  var gmean = require('compute-gmean')

  /* Get column from table method */
  function getCol(matrix, col) {
    var column = [];
    for (var i = 0; i < matrix.length; i++) {
      column.push(matrix[i][col]);
    }
    return column;
  }

  /* Get average from array */
  var average = function (arr) {
    if (typeof arr !== 'object' || arr.length === 0) return false;

    var key, totalNumber = 0;
    for (key in arr) totalNumber = totalNumber + Number(arr[key]);

    return totalNumber / arr.length;
  };

  /* Get (value-max)/(min-max) */
  function Emission(value, max, min) {
    return Number(((value - max) / (min - max)).toFixed(2));
  }

  /* Get (value-min)/(max-min) */
  function Emission2(value, max, min) {
    return Number(((value - min) / (max - min)).toFixed(2));
  }

  /* Get Social Welfare method (HDI * Health Index)*0.5 */
  function Social_Welfare(HDI, Health_index) {
    return (HDI * Health_index) * 0.5;
  }

  /* Get USDI method */
  function USDI_calc(EaC, CP, Economic, SW) {
    return ((EaC + CP + Economic + SW) * 0.25).toFixed(2);
  }

  export default {
    components: {
      HotTable,
      LMap,
      LTileLayer,
      LMarker,
      LTooltip,
      LIcon,
    },
    data: function () {
      return {
        input_data: input_json_data,
        input_cities: input_json_cities,
        input_calculation: input_json_calculation,
        input_graph: input_json_graph,
        input_geodata: input_json_geodata,
        table: 0,
        selected_city: "",
        selected_data: input_json_data,
        selected_city_geodata: ["Tokyo", "35.689487", "139.691711"],
        selected_city_column: ["Tokyo", 4.8, 60, 5.7, 33.1, 1.2, 0, 1, 100, 100, 10.6, 0.94, 100, 70759.60, 118000,
          3.6, 0.903, 0.978
        ],
        matrix: [],
        before_matrix: [],
        after_matrix: [],
        merged_matrix: [],
        check1: [],
        check2: [],
        check3: [],
        isShow: false,
        max_column: [10, 86, 57.3, 100, 14.8, 0, 1, 100, 100, 100, 1, 100, 100, 100, 14, 0.9, 1],
        min_column: [0.5, 18.5, 5.7, 27, 1.2, 0, 0, 0, 0, 0, 0, 0, 1000, 8900, 2.2, 0.3, 0.2],
        avarage_column: [4.6, 46.7, 22.5, 107.8, 6, 0, 1, 0.599, 70.1, 38.6, 0.17, 82.8, 9800, 52000, 5, 0.791,
          0.856259512
        ],

        attribution: "<a href='https://maps.gsi.go.jp/development/ichiran.html' target='_blank'>地理院タイル</a>",
        url: 'https://cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
        zoom: 3,
        icon: L.icon({
          iconUrl: 'static/images/baseball-marker.png',
          iconSize: [32, 37],
          iconAnchor: [16, 37]
        }),

        radar_series: [{
          name: 'Selected City',
          data: [0.5, 0.5, 0.5, 0.5],
        }, {
          name: 'Average 22 cities',
          data: [0.65, 0.22, 0.33, 0.50],
        }],
        radar_chartOptions: {
          labels: ['Energy and Climate', 'City Plannig', 'Economic', 'Social Welfare'],
          title: {
            text: ''
          },
          dropShadow: {
            enabled: true,
            blur: 1,
            left: 1,
            top: 1
          },
          yaxis: {
            show: true,
            showAlways: true,
            tickAmount: 5,
            min: 0,
            max: 1.0,
            forceNiceScale: false,
            floating: false,
          }
        },
      }
    },
    beforeCreate() {
      this.selected_city = "Tokyo";
    },
    methods: {
      reset_input_data: function () {
        location.reload();
      },
      select_city: function (event) {
        //console.log(typeof event.target.value);
        //console.log(event);
        this.selected_city = event.target.value;
        this.selected_city_column = this.$refs.input_data_ref.table.getDataAtCol(2 + this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.selected_city_geodata = this.$refs.input_geodata_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].name = event.target.value
        this.radar_series[0].data = this.$refs.input_graph_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].data = this.radar_series[0].data.slice(1, 5).map(function (value) {
          return round2(value);
        });
      },
    },
    computed: {
      first_column_input_data: function () {
        return getCol(this.input_data, 0);
      },
      Max_column: function () {
        return getCol(this.input_data, 24);
      },
      Min_column: function () {
        return getCol(this.input_data, 25);
      },
      GHG: function () {
        return Emission(4.6, 10, 0.5);
      },
      mu: function () {
        return gmean([0.55, 0.69, 1, 0.98, 1]);
      },
      Social_Welfare: function () {
        return Social_Welfare(0.79, 0.80);
      },
    },
    watch: {
      selected_city_column: {
        handler(val, old) {
          //input dataを転置
          this.matrix = transpose(this.input_data);
          //選択した都市のデータを抽出
          this.matrix = this.matrix[(2 + this.input_cities.findIndex(e =>
            e === this.selected_city_column[0]))];
          //更新した値を最大値と比較して、更新
          this.max_column = MaxSort(this.max_column, this.selected_city_column.slice(1, 18));
          this.min_column = MinSort(this.min_column, this.selected_city_column.slice(1, 18));

          //前後の２次元配列を抽出
          this.before_matrix = this.$refs.input_data_ref.table.getData(0, 0, 17, 1 + (this.input_cities.findIndex(e =>
            e === this.selected_city_column[0])))
          this.after_matrix = this.$refs.input_data_ref.table.getData(0, 3 + (this.input_cities.findIndex(e =>
            e === this.selected_city_column[0])), 17, 23);
          //前後のデータ,Max,Minと更新された都市の数値を行列として結合
          this.merged_matrix = pushTwoDimensionalArray(this.before_matrix, transpose([this.selected_city_column]), 1);
          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, this.after_matrix, 1);

          //平均値、最大値、最小値の更新
          var average_array = [];
          var max_array = [];
          var min_array = [];
          for (var i = 1; i < 18; i++) {
            average_array.push(average(this.merged_matrix[i].slice(2, 24)).toFixed(2));
            max_array.push(Math.max.apply(null, this.merged_matrix[i].slice(2, 24)).toFixed(2));
            min_array.push(Math.min.apply(null, this.merged_matrix[i].slice(2, 24)).toFixed(2));
          }
          //テーブルに反映
          var array2 = [];
          for (var i = 2; i < 24; i++) {
            array2.push(transpose(this.merged_matrix)[i]);
          }

          this.merged_matrix = pushTwoDimensionalArray(
            pushTwoDimensionalArray(transpose([transpose(this.merged_matrix)[0]]), transpose([
              ["Average 22 cities"].concat(average_array.map(function (element) {
                return String(element);
              }))
            ]), 1),
            transpose(array2), 1);

          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
            ["Max"].concat(max_array.map(function (element) {
              return String(element);
            }))
          ]), 1);
          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
            ["Min"].concat(min_array.map(function (element) {
              return String(element);
            }))
          ]), 1);
          this.$refs.input_data_ref.table.loadData(this.merged_matrix);

          // Input calculationの反映;
          // GHG Emissionを更新し、リストに格納
          var GHG = [];
          GHG = this.input_calculation[1].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[1].slice(1, 24).map(Number)[index]), Number(this[1].slice(24, 26)[0]),
              Number(this[1].slice(24, 26)[1]));
          }, this.merged_matrix);
          GHG = ["GHG Emission"].concat(GHG.map(function (element) {
            return String(element);
          }));

          // Noxを更新し、リストに格納
          var Nox = [];
          Nox = this.input_calculation[2].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[2].slice(1, 24).map(Number)[index]), Number(this[2].slice(24, 26)[0]),
              Number(this[2].slice(24, 26)[1]));
          }, this.merged_matrix);

          Nox = ["Nox"].concat(Nox.map(function (element) {
            return String(element);
          }));

          // Soxを更新し、リストに格納
          var Sox = [];
          Sox = this.input_calculation[3].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[3].slice(1, 24).map(Number)[index]), Number(this[3].slice(24, 26)[0]),
              Number(this[3].slice(24, 26)[1]));
          }, this.merged_matrix);

          Sox = ["Sox"].concat(Sox.map(function (element) {
            return String(element);
          }));

          // PMを更新し、リストに格納
          var PM = [];
          PM = this.input_calculation[4].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[4].slice(1, 24).map(Number)[index]), Number(this[4].slice(24, 26)[0]),
              Number(this[4].slice(24, 26)[1]));
          }, this.merged_matrix);

          PM = ["PM"].concat(PM.map(function (element) {
            return String(element);
          }));

          // EIを更新し、リストに格納
          var EI = [];
          EI = this.input_calculation[5].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[5].slice(1, 24).map(Number)[index]), Number(this[5].slice(24, 26)[0]),
              Number(this[5].slice(24, 26)[1]));
          }, this.merged_matrix);

          EI = ["EI"].concat(EI.map(function (element) {
            return String(element);
          }));

          // Energy and Climateを更新し、リストに格納
          var EaC = [];
          EaC = this.input_calculation[6].slice(1, 24).map(function (value, index) {
            return ((Number(GHG[index + 1]) * Number(Nox[index + 1]) * Number(Sox[index + 1]) * Number(PM[index +
              1]) * Number(EI[index + 1])) ** (1 / 5)).toFixed(2)
          }, GHG, Nox, Sox, PM, EI);

          EaC = ["Energy and Climate"].concat(EaC.map(function (element) {
            return String(element);
          }));

          // Water Accessibilityを更新し、リストに格納
          var WA = [];
          WA = this.input_calculation[7].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[7].slice(1, 24).map(Number)[index]), Number(this[7].slice(24, 26)[0]),
              Number(this[7].slice(24, 26)[1]));
          }, this.merged_matrix);

          WA = ["Water Accessibility"].concat(WA.map(function (element) {
            return String(element);
          }));

          // Share of wastewater treated (%)を更新し、リストに格納
          var Sowt = [];
          Sowt = this.input_calculation[8].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[8].slice(1, 24).map(Number)[index]), Number(this[8].slice(24, 26)[0]),
              Number(this[8].slice(24, 26)[1]));
          }, this.merged_matrix);

          Sowt = ["Share of wastewater treated (%)"].concat(Sowt.map(function (element) {
            return String(element);
          }));

          // Population with access to sanitation (%)を更新し、リストに格納
          var Pwats = [];
          Pwats = this.input_calculation[9].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[9].slice(1, 24).map(Number)[index]), Number(this[9].slice(24, 26)[0]),
              Number(this[9].slice(24, 26)[1]));
          }, this.merged_matrix);

          Pwats = ["Population with access to sanitation (%)"].concat(Pwats.map(function (element) {
            return String(element);
          }));

          // Transport networkを更新し、リストに格納
          var Tn = [];
          Tn = this.input_calculation[10].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[11].slice(1, 24).map(Number)[index]), Number(this[11].slice(24, 26)[0]),
              Number(this[11].slice(24, 26)[1]));
          }, this.merged_matrix);

          Tn = ["Transport network"].concat(Tn.map(function (element) {
            return String(element);
          }));

          // Share of waste collected and adequately disposed (%)を更新し、リストに格納
          var Sowcaa = [];
          Sowcaa = this.input_calculation[11].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[12].slice(1, 24).map(Number)[index]), Number(this[12].slice(24, 26)[0]),
              Number(this[12].slice(24, 26)[1]));
          }, this.merged_matrix);

          Sowcaa = ["Share of waste collected and adequately disposed (%)"].concat(Sowcaa.map(function (element) {
            return String(element);
          }));

          // City Planning を更新し、リストに格納
          var CP = [];
          CP = this.input_calculation[12].slice(1, 24).map(function (value, index) {
            return ((Number(WA[index + 1]) * Number(Sowt[index + 1]) * Number(Pwats[index + 1]) * Number(Tn[index +
              1]) * Number(Sowcaa[index + 1])) ** (1 / 5)).toFixed(2)
          }, WA, Sowt, Pwats, Tn, Sowcaa);

          CP = ["City Planning "].concat(CP.map(function (element) {
            return String(element);
          }));

          // GDP per person (current prices) (US$)を更新し、リストに格納
          var GDP = [];
          GDP = this.input_calculation[13].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[13].slice(1, 24).map(Number)[index]), Number(this[13].slice(24, 26)[0]),
              Number(this[13].slice(24, 26)[1]));
          }, this.merged_matrix);

          GDP = ["GDP per person (current prices) (US$)"].concat(GDP.map(function (element) {
            return String(element);
          }));

          // Average per-worker labor productivity level (US$)を更新し、リストに格納
          var Apwlpl = [];
          Apwlpl = this.input_calculation[14].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[14].slice(1, 24).map(Number)[index]), Number(this[14].slice(24, 26)[0]),
              Number(this[14].slice(24, 26)[1]));
          }, this.merged_matrix);

          Apwlpl = ["Average per-worker labor productivity level (US$)"].concat(Apwlpl.map(function (element) {
            return String(element);
          }));

          // Annual Rate of Unemployment (%)を更新し、リストに格納
          var ARoU = [];
          ARoU = this.input_calculation[15].slice(1, 24).map(function (value, index) {
            return Emission(Number(this[15].slice(1, 24).map(Number)[index]), Number(this[15].slice(24, 26)[0]),
              Number(this[15].slice(24, 26)[1]));
          }, this.merged_matrix);

          ARoU = ["Annual Rate of Unemployment (%)"].concat(ARoU.map(function (element) {
            return String(element);
          }));

          // Economic  を更新し、リストに格納
          var Economic = [];
          Economic = this.input_calculation[16].slice(1, 24).map(function (value, index) {
            return ((Number(GDP[index + 1]) * Number(Apwlpl[index + 1]) * Number(ARoU[index + 1])) ** (1 / 3))
              .toFixed(2)
          }, GDP, Apwlpl, ARoU);

          Economic = ["Economic "].concat(Economic.map(function (element) {
            return String(element);
          }));

          // HDIを更新し、リストに格納
          var HDI = [];
          HDI = this.input_calculation[17].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[16].slice(1, 24).map(Number)[index]), Number(this[16].slice(24, 26)[0]),
              Number(this[16].slice(24, 26)[1]));
          }, this.merged_matrix);

          HDI = ["HDI"].concat(HDI.map(function (element) {
            return String(element);
          }));

          // Health Index を更新し、リストに格納
          var HI = [];
          HI = this.input_calculation[18].slice(1, 24).map(function (value, index) {
            return Emission2(Number(this[17].slice(1, 24).map(Number)[index]), Number(this[17].slice(24, 26)[0]),
              Number(this[17].slice(24, 26)[1]));
          }, this.merged_matrix);

          HI = ["Health Index "].concat(HI.map(function (element) {
            return String(element);
          }));

          // Social Welfare  を更新し、リストに格納
          var SW = [];
          SW = this.input_calculation[19].slice(1, 24).map(function (value, index) {
            return ((Number(HDI[index + 1]) * Number(HI[index + 1])) ** (0.5)).toFixed(2)
          }, HDI, HI);

          SW = ["Social Welfare"].concat(SW.map(function (element) {
            return String(element);
          }));

          // USDI  を更新し、リストに格納
          var USDI = [];
          USDI = this.input_calculation[20].slice(1, 24).map(function (value, index) {
            return USDI_calc(Number(EaC[index + 1]), Number(CP[index + 1]), Number(Economic[index + 1]), Number(SW[
              index + 1]));
          }, EaC, CP, Economic, SW);

          USDI = ["USDI"].concat(USDI.map(function (element) {
            return String(element);
          }));

          this.$refs.input_calculation_ref.table.loadData([this.input_calculation[0], GHG, Nox, Sox, PM, EI, EaC, WA,
            Sowt, Pwats, Tn, Sowcaa, CP, GDP, Apwlpl, ARoU, Economic, HDI, HI, SW, USDI
          ]);
          this.input_graph = [this.input_graph[0], EaC, CP, Economic, SW, USDI];
          this.$refs.input_graph_ref.table.loadData(this.input_graph);
          this.radar_series[1].data = [Number(EaC[1]), Number(CP[1]), Number(Economic[1]), Number(SW[1])]
          var rader_index = this.input_graph[0].findIndex(e => e === this.selected_city_column[0]);
          this.radar_series[0].data = [Number(EaC[rader_index]), Number(CP[rader_index]), Number(Economic[rader_index]),
            Number(SW[rader_index])
          ];
          //console.log(this.input_graph[0].findIndex(e =>e === this.selected_city_column[0]));


        },
        deep: true
      }
    }
  };
</script>

